.training-hero {
  background-image: url(../images/training.png);
  background-size: cover;
  background-repeat: no-repeat;
  width: 98%;
  height: 98vh;
  margin-left: auto;
  margin-right: auto;
}

.training-mobile-hero {
  background-image: url(../images/training.png);
  background-size: cover;
  background-repeat: no-repeat;
  width: 98%;
  height: 98vh;
  margin-left: auto;
  margin-right: auto;
  background-position-x: 80%;
  filter: brightness(0.4);
  -webkit-filter: brightness(0.4);
}